*,
*::before,
*::after {
  box-sizing: border-box;
}
.body {
  font-family: 'TeX Gyre Heros', sans-serif;
  font-weight: 400;
}
.main {
  padding-bottom: 50px;
}
ul {
  padding: 0 0 0 0;
  margin: 0 0 0 0;
}
img {
  object-fit: contain;
}
li {
  list-style: none;
}
.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}
@keyframes width {
  0% {
    opacity: 0;
    transform: translateX(0);
  }
  10% {
    opacity: 0.7;
  }
  90% {
    opacity: 0;
  }
  100% {
    transform: translateX(-45%);
  }
}
@keyframes show-section {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes height-block {
  0% {
    height: 0;
  }
  100% {
    height: 100%;
  }
}
.menu {
  display: flex;
  justify-content: space-between;
  padding: 27px 0 27px 0;
}
.menu__list {
  display: flex;
  flex: 1;
}
.menu__link {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  text-decoration: none;
  color: #000000;
}
.menu__item {
  margin-right: 40px;
}
.menu__item:last-child {
  margin-right: 0;
}
.menu__list--right {
  display: flex;
  justify-content: flex-end;
}
.menu__list--right .menu__item {
  margin-right: 30px;
  position: relative;
}
.menu__list--right .menu__item:last-child {
  margin-right: 0;
}
.menu__button {
  background-color: transparent;
  cursor: pointer;
  border: none;
  padding: 0;
}
.menu__notification {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 15px;
  height: 15px;
  top: -6px;
  right: -8px;
  font-size: 10px;
  line-height: 8px;
  background-color: #000000;
  color: #FFFFFF;
  border-radius: 100px;
}
.header {
  position: sticky;
  background-color: #FFFFFF;
  top: 0;
  z-index: 3;
}
.header::after {
  content: '';
  width: 100%;
  border-bottom: 1px solid #C8C8C8;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  position: absolute;
}
.breadcrumb {
  margin-top: 50px;
}
.breadcrumb__list {
  display: flex;
}
.breadcrumb__link {
  text-decoration: none;
  font-size: 12px;
  line-height: 15px;
  color: #6E6E6E;
}
.breadcrumb__link--noactive {
  color: #000000;
}
.breadcrumb__item {
  margin-right: 10px;
}
.order {
  display: grid;
  grid-template-columns: 2fr 400px;
  gap: 40px;
  padding: 0 0 50px 0;
}
.caption__heading--h1 {
  font-size: 40px;
  font-weight: 400;
  line-height: 42px;
  color: #000000;
  margin: 40px 0 50px 0;
}
.caption__list {
  display: flex;
  align-items: center;
}
.caption__text {
  font-size: 14px;
  line-height: 18px;
  color: #6E6E6E;
}
.caption__link {
  font-size: 14px;
  line-height: 18px;
  color: #000000;
  text-decoration: none;
}
.caption__item--padding {
  margin-left: 20px;
}
.caption__item--padding:last-child {
  margin-left: 5px;
}
.summary__text {
  color: #000000;
  font-size: 28px;
  line-height: 30px;
  margin: 0 0 25px 0;
}
.card__size {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
  background-color: #F1F1F1;
  padding: 11px;
  cursor: pointer;
  border: none;
  width: 30px;
  height: 30px;
}
.card__sign {
  font-size: 12px;
  line-height: 15px;
  color: #000000;
}
.card__list--row {
  display: flex;
}
.card__item--margin {
  margin-right: 5px;
}
.card__item--margin:last-child {
  margin-right: 0;
}
.card__wrapper {
  display: flex;
}
.card__img {
  width: 130px;
  height: 160px;
  object-fit: contain;
}
.card__caption {
  color: #000000;
  font-size: 20px;
  line-height: 24px;
  margin: 0 0 15px 0;
  width: 197px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.card__significance {
  font-size: 12px;
  line-height: 15px;
  color: #6E6E6E;
}
.card__number {
  color: #000000;
  font-size: 12px;
  line-height: 15px;
  font-weight: 700;
}
.card__item--row {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  justify-items: center;
  padding: 25px 0 25px 0;
  position: relative;
  border-top: 1px solid #C8C8C8;
  border-bottom: 1px solid #C8C8C8;
  min-height: 160px;
}
.card__wrapper--column {
  display: flex;
  flex-direction: column;
  margin-left: 12px;
  margin-right: 35px;
}
.card__count {
  min-width: 153px;
  margin: 0 30px;
}
.card__block {
  margin-top: 7px;
}
.card__price {
  display: grid;
  grid-auto-columns: max-content;
}
.card__inner {
  margin: 5px 0 5px 0;
}
.card__size--js {
  transition: 0.5s ease;
  background-color: #000000;
}
.card__size--js .card__sign {
  color: #FFFFFF;
}
.checkbox__input {
  width: 20px;
  height: 20px;
  padding: 11px;
  opacity: 0;
}
.checkbox__item {
  position: relative;
  margin-right: 6px;
}
.checkbox__item:last-child {
  margin-right: 0;
}
.checkbox__span::before {
  content: "";
  position: absolute;
  left: 0;
  top: calc(50% - 12px);
  width: 20px;
  height: 20px;
  border: 1px solid #DADADA;
  background-color: #FFFFFF;
  border-radius: 50%;
  z-index: -1;
}
.checkbox__span--black::before {
  background: radial-gradient(circle, #000000 0%, #000000 57%, transparent 50%, transparent 100%);
}
.checkbox__span--brown::before {
  background: radial-gradient(circle, #AE6D0C 0%, #AE6D0C 57%, transparent 50%, transparent 100%);
}
.checkbox__span--blue::before {
  background: radial-gradient(circle, #106DA1 0%, #106DA1 57%, transparent 50%, transparent 100%);
}
.checkbox__input:checked + .checkbox__span::before {
  border: 1px solid #000000;
}
.checkbox__list {
  display: flex;
  margin-top: 12px;
}
.price__old {
  font-size: 12px;
  line-height: 15px;
  text-decoration: line-through;
  color: #6E6E6E;
}
.price__new {
  font-size: 20px;
  line-height: 24px;
  font-weight: 700;
}
.count__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 5px;
  background-color: #F1F1F1;
  padding: 5px 0 5px 0;
}
.count__button {
  border: none;
  background-color: transparent;
  cursor: pointer;
  width: 32px;
  height: 32px;
  margin: 0 10px 0 10px;
}
.count__quantity {
  font-size: 16px;
  line-height: 21px;
}
.count__minus::before {
  content: '';
  display: block;
  width: 11px;
  height: 2px;
  background-color: #000000;
}
.count__plus {
  position: relative;
}
.count__plus::before {
  content: '';
  display: block;
  width: 11px;
  height: 2px;
  background-color: #000000;
}
.count__plus::after {
  content: '';
  display: block;
  width: 2px;
  height: 11px;
  background-color: #000000;
  position: absolute;
  top: -4px;
  left: -5px;
}
.delete__button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  position: relative;
  width: 30px;
  height: 15px;
}
.delete__symbol {
  position: relative;
  display: flex;
}
.delete__symbol::before {
  display: block;
  content: '';
  background-color: #000000;
  width: 20px;
  height: 2px;
  transform: rotate(-45deg);
  position: absolute;
}
.delete__symbol::after {
  display: block;
  content: '';
  background-color: #000000;
  width: 20px;
  height: 2px;
  transform: rotate(45deg);
  position: absolute;
}
.empty__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 25px 0 25px;
  margin: 25px 0 25px 0;
  background-color: #F1F1F1;
}
.empty__wrapper--js {
  display: flex;
}
.empty__wrapper--js .empty__wrapper {
  display: flex;
  width: 100%;
}
.empty__text {
  color: #6E6E6E;
  font-size: 16px;
  line-height: 21px;
}
.empty__recovery {
  font-size: 14px;
  line-height: 18px;
  text-decoration: none;
  color: #000000;
  font-weight: 700;
}
.detail {
  position: relative;
}
.detail__item {
  position: relative;
  width: 100%;
  margin-right: 20px;
  margin-bottom: 20px;
}
.detail__item:last-child {
  margin-right: 0;
}
.detail__label {
  font-size: 12px;
  font-weight: 400;
  line-height: 15px;
  position: absolute;
  top: 50%;
  left: 25px;
  transform: translateY(-50%);
  opacity: 0.6;
  pointer-events: none;
  transition: top 0.3s, font-size 0.3s;
  background-color: #FFFFFF;
}
.detail__input {
  width: 100%;
  border: 1px solid #C8C8C8;
  outline: none;
  font-size: 16px;
  line-height: 21px;
  padding: 15px 20px 15px 20px;
  border-radius: 5px;
  transition: border-color 0.3s, box-shadow 0.3s;
}
.detail__heading {
  font-size: 28px;
  line-height: 30px;
  margin: 40px 0;
}
.detail__list {
  display: flex;
}
.detail__input:focus + .detail__label,
.detail__input:valid + .detail__label {
  top: 0;
  font-size: 12px;
  opacity: 1;
}
.detail__inner {
  position: relative;
}
.detail__search {
  background-color: transparent;
  border: none;
  cursor: pointer;
  position: absolute;
  top: -57px;
  right: 7px;
}
.detail__list--map {
  position: absolute;
  top: 0;
  left: calc(100% + 40px);
}
.detail__map {
  width: 400px;
  height: 450px;
}
.payment__input {
  width: 0;
  height: 0;
}
.payment__input:checked + .payment__label::before {
  background: radial-gradient(circle, #000 0, #000 35%, transparent 50%, transparent 100%);
}
.payment__label {
  position: relative;
  padding-left: 50px;
  font-size: 16px;
  line-height: 21px;
}
.payment__label::before {
  content: "";
  position: absolute;
  left: 0;
  top: calc(50% - 12px);
  width: 22px;
  height: 22px;
  border: 1px solid #DADADA;
  background-color: #FFFFFF;
  border-radius: 50%;
}
.payment__item {
  margin-bottom: 21px;
}
.payment__heading {
  font-size: 28px;
  line-height: 30px;
  margin: 40px 0;
}
.comment__textarea {
  resize: none;
  width: 100%;
  font-size: 16px;
  font-weight: 400;
  line-height: 21px;
  color: #6E6E6E;
  padding: 20px 0 0 15px;
  border: none;
  outline: 1px solid #6E6E6E;
}
.comment__value {
  font-size: 12px;
  line-height: 15px;
  color: #6E6E6E;
}
.comment__heading {
  font-size: 28px;
  line-height: 30px;
  margin: 40px 0;
}
.total {
  outline: 1px solid #6E6E6E;
  width: 100%;
  height: min-content;
  padding: 30px 20px 30px 20px;
  margin-top: 100px;
}
.total__list {
  display: flex;
  flex-direction: column;
  padding-bottom: 5px;
}
.total__item {
  display: flex;
  justify-content: space-between;
}
.total__price--sale {
  color: #D92C2C;
}
.total__price {
  font-size: 20px;
  line-height: 24px;
}
.total__text {
  font-size: 20px;
  line-height: 24px;
  margin-bottom: 20px;
}
.total__small {
  font-size: 16px;
  line-height: 21px;
}
.total__list--border {
  position: relative;
}
.total__list--border::after {
  content: '';
  position: absolute;
  bottom: 0;
  width: 100%;
  display: block;
  border-bottom: 1px solid #C8C8C8;
}
.total__item--column {
  flex-direction: column;
}
.total__list--row {
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  position: relative;
  padding: 20px 0 20px 0;
}
.total__list--row::before {
  content: '';
  position: absolute;
  bottom: 0;
  width: 100%;
  display: block;
  border-bottom: 1px solid #C8C8C8;
}
.total__checkbox {
  opacity: 0;
  pointer-events: none;
  height: 0;
}
.total__label {
  position: relative;
  display: flex;
  width: 41px;
  height: 15px;
}
.total__slider {
  background-color: #C8C8C8;
  opacity: 0.5;
  border-radius: 50px;
  bottom: 0;
  cursor: pointer;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: 0.4s;
}
.total__slider::before {
  background-color: #6E6E6E;
  bottom: 0;
  content: "";
  height: 18px;
  width: 18px;
  left: 0;
  position: absolute;
  transition: 0.4s;
  border-radius: 50%;
}
.total__checkbox:checked + .total__slider {
  background-color: #66bb6a;
}
.total__checkbox:checked + .total__slider::before {
  transform: translateX(26px);
}
.total__input {
  border: 1px solid #C8C8C8;
  padding: 15px 20px 15px 20px;
  width: 100%;
}
.total__send {
  background-color: #000000;
  border: none;
  cursor: pointer;
  width: 100%;
  padding: 15px 70px 15px 70px;
  margin-top: 20px;
  margin-bottom: 10px;
}
.total__span {
  color: #FFFFFF;
  font-size: 16px;
  line-height: 21px;
}
.total__strong {
  font-size: 28px;
  font-weight: 700;
  line-height: 30px;
  margin: 25px 0 25px 0;
}
.total__map {
  position: absolute;
  width: 400px;
  height: 450px;
  top: 0;
  left: calc(100% + 40px);
}
.footer {
  border-top: 1px solid #6E6E6E;
}
.footer__heading {
  font-size: 28px;
  line-height: 30px;
  font-weight: 400;
}
.footer__send {
  display: flex;
  flex-direction: row-reverse;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.footer__text {
  font-weight: 700;
  font-size: 16px;
  line-height: 21px;
  margin-left: 10px;
}
.footer__input {
  width: 100%;
  border: 1px solid #6E6E6E;
  border-radius: 4px;
  padding: 15px 155px 15px 20px;
  font-size: 16px;
  line-height: 21px;
  overflow: scroll;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.footer__group {
  position: relative;
}
.footer__item--position {
  position: absolute;
  right: 15px;
  top: 15px;
